:root {
  --primary-cl: #e8e8e2;
  --primary-cl-20: rgba(232, 232, 226, .2);
  --primary-cl-30: rgba(232, 232, 226, .3);
  --primary-cl-60: rgba(232, 232, 226, .6);
  --primary-cl-70: rgba(232, 232, 226, .7);
  --lite-color: #fafafa;
  --lite-green: #6d8434;
  --dark-green: #586f1f;
  --dark-cl: #010101;
  --dark-cl-10: rgba(1, 1, 1, .1);
  --dark-cl-20: rgba(1, 1, 1, .2);
  --dark-cl-30: rgba(1, 1, 1, .3);
  --dark-cl-50: rgba(1, 1, 1, .5);
  --dark-cl-70: rgba(1, 1, 1, .7);
  --grey-cl: #a3a3a3;
  --grey-cl-30: rgba(163, 163, 163, .3);
  --trans: 250ms cubic-bezier(.4, 0, .2, 1);
}

@font-face {
  font-family: Neue Montreal;
  font-display: swap;
  src: url("NeueMontreal-Regular.7559b1a8.woff2") format("woff2"), url("NeueMontreal-Regular.f021a830.woff") format("woff");
